@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
* {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
}

body {
  background-color: white;
  font-family: "PT Sans", sans-serif;
  color: #141414;
  scroll-behavior: smooth;
  overscroll-behavior: none;
}

.wrapper {
  margin: auto;
  width: 1000px;
}

header {
  height: 100vh;
  background-image: url("../img/background.jpg");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.gallery-header {
  background-image: none;
  height: auto;
}
.gallery-header .main-nav {
  position: unset;
  margin-bottom: 5%;
}

.main-nav {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 1.5rem 0;
  background-color: #060c1bdc;
  z-index: 100;
}
.main-nav .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-nav ul {
  width: 60%;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 1.25rem;
}
.main-nav ul a {
  color: #fff;
  opacity: 0.85;
}
.main-nav ul a:hover {
  text-decoration: underline;
  opacity: 1;
}

.logo {
  font-size: 2rem;
  font-style: italic;
  color: #7a7a7a;
  opacity: 0.9;
}
.logo a {
  color: #fff;
}

h1 {
  position: absolute;
  top: 45%;
  text-align: center;
  font-size: 4rem;
  background-color: #060c1bdc;
  width: max-content;
  margin-left: 25%;
  color: #fff;
}

h2 {
  font-size: 2rem;
}

.projects {
  padding: 3rem;
}

.projects-list {
  display: flex;
  justify-content: space-between;
  padding-top: 2rem;
}

.projects-list__item {
  box-sizing: border-box;
  width: 30%;
  background-color: white;
  color: black;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  border: 1px solid #141414;
  box-shadow: 6px -6px 5px rgba(0, 0, 0, 0.3);
}
.projects-list__item img {
  width: 100%;
  margin: 5% 0;
}
.projects-list__item h3 {
  margin-bottom: 1rem;
}
.projects-list__item p {
  margin-bottom: 1rem;
}
.projects-list__item a {
  color: #060c1b;
}
.projects-list__item a:hover {
  opacity: 0.7;
}

.about {
  background-color: #060c1bdc;
  color: #fff;
  padding: 7rem 0;
}

.about-content {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}
.about-content img {
  width: 25%;
}
.about-content p {
  width: 70%;
}

.skills {
  padding: 3rem;
}

.skills-list {
  display: flex;
  justify-content: space-around;
  margin: 5em 0;
}
.skills-list :not(p):nth-child(2n) {
  background-color: rgba(128, 128, 128, 0.287);
}

.skills-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20%;
  border: 1px solid #141414;
  box-shadow: 6px -6px 5px rgba(0, 0, 0, 0.3);
  padding: 1em 0;
}
.skills-list__item svg {
  width: 75px;
}
.skills-list__item svg path {
  fill: #101f47dc;
}

.bar {
  width: 70%;
  height: 20px;
  background-color: #7a7a7a;
  position: relative;
  margin-top: 1em;
}

.bar-status {
  width: 70%;
  height: 20px;
  background-color: #101f47dc;
  position: absolute;
  top: 0;
  left: 0;
}

footer {
  background-color: #060c1bdc;
  padding: 3em 0 2em 0;
}
footer h2 {
  text-align: center;
  color: white;
  padding-bottom: 2em;
}
footer p {
  color: #fff;
  text-align: center;
  margin-top: 2.5em;
}

form {
  width: 50%;
  margin: auto;
}

.form-input {
  position: relative;
  display: flex;
  justify-content: space-between;
}

label {
  padding-bottom: 7px;
  color: white;
}

input {
  height: 50px;
  width: 70%;
  border-radius: 6px;
  margin-bottom: 1.5em;
}

textarea {
  border-radius: 6px;
  width: 70%;
}

input:hover,
input:active,
input:focus,
textarea:hover {
  outline: 1px #101f47dc solid;
}

.button {
  display: block;
  margin: auto;
  margin-top: 33px;
  width: 312px;
  height: 52px;
  border: none;
  background-color: #101f47dc;
  color: white;
  font-size: 18px;
  border-radius: 6px;
}

#scrollBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: #101f47dc;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
  font-size: 18px;
}

.grid-container {
  margin: auto;
  margin-bottom: 3em;
  display: grid;
  width: 80%;
  grid-auto-flow: row dense;
  grid-template-columns: 1fr 1fr 35% 35%;
  grid-template-rows: 0.5fr 0.5fr 1fr 1fr 1fr;
  gap: 1rem 1rem;
  justify-items: center;
  grid-template-areas: "a a b c" "a a d c" "e h h c" "f h h i" "g h h i";
}

.grid-item {
  width: 100%;
  height: 100%;
}
.grid-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 6px;
}
.grid-item :hover {
  transform: scale(1.05);
  box-shadow: 6px -6px 5px rgba(0, 0, 0, 0.3);
}

.a {
  grid-area: a;
}

.b {
  grid-area: b;
}

.c {
  grid-area: c;
}

.d {
  grid-area: d;
}

.e {
  grid-area: e;
}

.f {
  grid-area: f;
}

.g {
  grid-area: g;
}

.h {
  grid-area: h;
}

.i {
  grid-area: i;
}